import React from "react";

import "./SubCategoriesCardLeft.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function SubCategoriesCardLeft(prop) {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="sub-categories-cards-left-main-container">
      <div className="sub-categories-cards-left-main-container-box-1">
        <div
          className={`sub-categories-cards-left-main-container-box-1a ${
            inViewBox1a ? "sub-categories-cards-left-slide-in-animation" : ""
          }`}
        >
          <h3 style={{ visibility: `${prop.heading ? "visible" : "hidden"}` }}>
            {prop.heading}
          </h3>

          <p ref={refBox1a}>{prop.paragrapgh}</p>

          <button
            style={{ visibility: `${prop.button ? "visible" : "hidden"}` }}
          >
            {prop.button}
          </button>
        </div>
      </div>

      <div className="sub-categories-cards-left-main-container-box-2">
        <img src={prop.picture} alt="pic1" />
      </div>
    </div>
  );
}

SubCategoriesCardLeft.defaultProps = {
  /* heading: "Enter your heading here", */
  paragrapgh: "Enter your text here",
  /* button: "Text for first button", */
  picture: pic1,
};
