import React, { useRef , useEffect } from "react";

import { useInView } from "react-intersection-observer";

import "./PointerCards.css";

export default function PointerCards(prop) {
  const [ref, inView] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const pointerRef = useRef(null);

  useEffect(() => {
    if (inView) {
      pointerRef.current.classList.add("pointers-cards-slide-in");
    } /* else {
      h1Ref.current.classList.remove("animate-h1");
    } */
  }, [inView]);

  return (
    <div ref={ref} >
      <div  ref={pointerRef} className="pointers-card-main-container">
        <h1>{prop.heading}</h1>
        <p>{prop.paragraph}</p>
      </div>
    </div>
  );
}
