import React from "react";

import "./SubCategoriesBanner.css";

import backgroundImage from "./banner.jpg";

export default function SubCategoriesBanner(prop) {
  return (
    <div
      className="sub-categories-banner"
      style={{ backgroundImage: `url(${prop.bannerBackgroundImage})` }}
    >
      <h1>{prop.bannerHeading}</h1>

      <p>{prop.bannerParagraph}</p>

      <div className="sub-categories-banner-buttons">
        <button>{prop.bannerButton1}</button>
        <button>{prop.bannerButton2}</button>
      </div>
    </div>
  );
}

SubCategoriesBanner.defaultProps = {
  bannerHeading: "Enter your heading here",
  bannerParagraph: "Enter your paragraph here",
  bannerButton1: "Text for first button",
  bannerButton2: "Text for second button",
  bannerBackgroundImage: backgroundImage,
};
