import React, { useEffect, useRef } from "react";

import { useInView } from "react-intersection-observer";

import PointerCards from "../social_media_management_pointer_cards/PointerCards";

import "./Pointers.css";

export default function Pointers() {
  const [ref, inView] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const h1Ref = useRef(null);

  useEffect(() => {
    if (inView) {
      h1Ref.current.classList.add("animate-h1");

    } /* else {
      h1Ref.current.classList.remove("animate-h1");
    } */
  }, [inView]);

  return (
    <>
      <div ref={ref} className="social-media-management-pointers">
        {inView ? (
          <div ref={h1Ref}>
            <h1 /* ref={h1Ref} */>
              Why do companies need social media management?
            </h1>

            <p>
              Social media management is crucial for companies to effectively
              utilize social media platforms as a part of their marketing and
              communication strategies, connect with their audience, and achieve
              their business goals.
            </p>
          </div>
        ) : (
          <h1>"Loading..."</h1>
        )}
      </div>

      <div className="social-media-management-pointer-div-2">
        <PointerCards
          heading="Brand Awareness"
          paragraph="Social media sites give businesses the chance to develop and improve their brand presence. Companies may build a strong online presence and reach a larger audience with the aid of efficient social media management."
        />

        <PointerCards
          heading="Customer Engagement"
          paragraph="Social media enables businesses to interact with their clients directly, answering their questions, resolving their issues, and offering assistance. Social media engagement with clients on a regular basis promotes loyalty and strengthens connections."
        />

        <PointerCards
          heading="Marketing and promotion"
          paragraph="Social media sites are effective marketing instruments. Businesses may use social media management to produce and disseminate interesting content, advertise their goods and services, execute marketing campaigns, and increase website traffic."
        />

        <PointerCards
          heading="Reputation management"
          paragraph="Customers frequently talk about and share their brand experiences on social media. Companies can monitor and manage their online reputation with the help of effective social media management, quickly responding to any unfavorable comments or reviews."
        />

        <PointerCards
          heading="Market research"
          paragraph="Social media offers useful information on the tastes, trends, and views of consumers. Companies may obtain market research data and better understand their target audience by actively monitoring and analyzing social media interactions."
        />

        <PointerCards
          heading="Competitive Advantage"
          paragraph="In today's digital environment, having a strong social media presence may give firms a competitive edge. With social media management, companies can track competitors, identify market trends, and stay current."
        />

        <PointerCards
          heading="Lead generation and sales"
          paragraph="There are prospects on social media sites."
        />
      </div>
    </>
  );
}
